import {Field, Form, Formik} from "formik";
import React from "react";
import FormBackLogin from "../../../Pages/LoginPage/LoginPage";







const ProfileDataForm = ({profile,onSubmit}) => (



    <div>
        <h1>Anywhere in your app!</h1>
        <Formik
            initialValues={{
                FullName: profile.fullName,
                lookingForAJob: false,
                lookingForAJobDescription: profile.lookingForAJobDescription,
                aboutMe: profile.aboutMe,
                contacts:{
                    facebook: profile.contacts.facebook,
                    website: profile.contacts.website,
                    vk: profile.contacts.vk,
                    twitter: profile.contacts.twitter,
                    instagram: profile.contacts.instagram,
                    youtube: profile.contacts.youtube,
                    github: profile.contacts.github,
                    mainLink:profile.contacts.mainLink
                }

            }}
            onSubmit={(values, ) => {
                onSubmit(values)
            }}
        >
            {(props) => (
                <Form>
                    <Field name="FullName" placeholder="FullName"  />
                    <td/>
                    <Field name="lookingForAJob" type="checkbox"  />
                    <td/>
                    <Field name="lookingForAJobDescription"  placeholder="lookingForAJobDescription"  />
                    <td/>
                    <Field name="aboutMe"  placeholder="aboutMe" />
                    <td/>
                    <Field name="contacts.facebook" />
                    <td/>
                    <Field name="contacts.website" />
                    <td/>
                    <Field name="contacts.vk" />
                    <td/>
                    <Field name="contacts.twitter" />
                    <td/>
                    <Field name="contacts.instagram" />
                    <td/>
                    <Field name="contacts.github" />
                    <td/>
                    <Field name="contacts.mainLink" />






                    <button type="submit">Submit</button>
                </Form>
            )}
        </Formik>
    </div>
);



export default ProfileDataForm