import React, {Component} from 'react';
import './App.css';
import 'antd/dist/antd.css';
import {Layout, Menu, Breadcrumb} from 'antd';
import {BrowserRouter, HashRouter, Link, Redirect, Route, Switch, withRouter} from 'react-router-dom'
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import Dialog from "./components/Dialog/Dialogcontainer";
import ProfileContainer from "./components/Profile/ProfileContainer";
import store from "./redux/redux-store";
import {Header} from "./components/Heder/Hader";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./redux/app-reducer";
import {compose} from "redux";
import Preloader from "./components/common/Preloader/Preloader";
import LoginPage from "./Pages/LoginPage/LoginPage";
import  {UsersPage} from "./components/Usersr/UsersContainer";
import ChatPage from "./Pages/Cat/ChatPache";

const { SubMenu } = Menu;
const {  Content, Sider } = Layout;



class App extends Component {
	componentDidMount() {
		this.props.initializeApp();
	}
	render() {
		if (!this.props.initialized) {
			return <Preloader />
		}
		return(
		<Layout>
			<Header/>
			<Layout>
				<Sider width={200} className="site-layout-background">
					<Menu
						mode="inline"
						defaultSelectedKeys={['1']}
						defaultOpenKeys={['sub1']}
						style={{ height: '100%', borderRight: 0 }}
					>
						<SubMenu key="sub1" icon={<UserOutlined />} title="subnav 1">
							<Menu.Item key="1" ><Link to={'/dialogs'}>Dialog</Link></Menu.Item>
							<Menu.Item key="2"><Link to={'/profile'}>Profile</Link></Menu.Item>
							<Menu.Item key="3"><Link to={'/users'}>Users</Link></Menu.Item>
							<Menu.Item key="4"><Link to={'/chat'}>Chat</Link></Menu.Item>
						</SubMenu>
						<SubMenu key="sub2" icon={<LaptopOutlined />} title="subnav 2">
							<Menu.Item key="5">option5</Menu.Item>
							<Menu.Item key="6">option6</Menu.Item>
							<Menu.Item key="7">option7</Menu.Item>
							<Menu.Item key="8">option8</Menu.Item>
						</SubMenu>
						<SubMenu key="sub3" icon={<NotificationOutlined />} title="subnav 3">
							<Menu.Item key="9">option9</Menu.Item>
							<Menu.Item key="10">option10</Menu.Item>
							<Menu.Item key="11">option11</Menu.Item>
							<Menu.Item key="12">option12</Menu.Item>
						</SubMenu>
					</Menu>
				</Sider>
				<Layout style={{ padding: '0 24px 24px' }}>
					<Breadcrumb style={{ margin: '16px 0' }}>
						<Breadcrumb.Item>Home</Breadcrumb.Item>
						<Breadcrumb.Item>List</Breadcrumb.Item>
						<Breadcrumb.Item>App</Breadcrumb.Item>
					</Breadcrumb>
					<Content
						className="site-layout-background"
						style={{
							padding: 24,
							margin: 0,
							minHeight: 280,
						}}
					>
						<Switch>
							<Route exact path='/'
								   render={() => <Redirect to={"/profile"}/>}/>
							<Route path='/dialogs'
								   render={() => <Dialog/>}/>
							<Route path='/profile/:userId?'
								   render={ () => <ProfileContainer /> }/>
							<Route path='/login'
								   render={()=><LoginPage/>}/>
							<Route path='/users'
								   render={()=><UsersPage/>}/>
							<Route path='/chat'
								   render={()=><ChatPage/>}/>

						</Switch>
					</Content>
				</Layout>
			</Layout>
		</Layout>
	)
}}
	const mapStateToProps = (state) => ({
	initialized: state.app.initialized
})

	let AppContainer = compose(
	withRouter,
	connect(mapStateToProps, {initializeApp}))(App);

	const SamuraiJSApp = (props) => {
	return <BrowserRouter >
		<Provider store={store}>
			<AppContainer />
		</Provider>
	</BrowserRouter>
}







export default SamuraiJSApp;
